import React, { useState, useEffect, useRef } from 'react';
import { useNavigate, useSearchParams } from 'react-router-dom';
import {
  Box,
  Paper,
  Typography,
  TextField,
  Button,
  Select,
  MenuItem,
  FormControl,
  InputLabel,
  SelectChangeEvent,
  Accordion,
  AccordionSummary,
  AccordionDetails,
  Slider,
} from '@mui/material';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import styled from 'styled-components';
import { createChart, ColorType, IChartApi, ISeriesApi } from 'lightweight-charts';
import axios from 'axios';
import settings from "../settings"
import { motion } from 'framer-motion';

const Logo = styled(motion.h1)`
  font-size: 1.8rem;
  font-weight: 700;
  background: linear-gradient(45deg, #1976d2, #42a5f5);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-top: 0;
  cursor: pointer;
`;

const TerminalContainer = styled(Box)`
  padding: 20px;
  background-color: #121212;
  height: 100vh;

  @media (max-width: 900px) {
    height: auto;
  }
`;

const ParameterPanel = styled(Paper)`
  padding: 20px;
  height: 100%;
  overflow-y: auto;
  min-width: 280px;
  flex: 1;
  scrollbar-width: none;
  -ms-overflow-style: none;
`;

const ChartContainer = styled(Paper)`
  height: 100%;
  display: flex;
  flex-direction: column;
  flex: 3;
  position: relative;
  overflow: hidden;
`;

const ChartWrapper = styled.div`
  position: relative;
  width: 100%;
  height: 100%;
  min-height: 300px;
`;

type Timeframe = '1Min' | '5Min' | '15Min' | '1Hour' | '4Hour' | '24Hour';

const multiplier_map = {
  "1Min": 1,
  "5Min": 5,
  "15Min": 15,
  "1Hour": 60,
  "4Hour": 240,
  "24Hour": 1440
};

interface Parameters {
  symbol: string;
  timeframe: Timeframe;
  patternLength: number;
  resultLength: number;
  accuracyPower: number;
  volatilityAdjustment: number;
}

const TerminalPage: React.FC = () => {
  const [searchParams, setSearchParams] = useSearchParams();
  const chartContainerRef = useRef<HTMLDivElement>(null);
  const chartRef = useRef<IChartApi | null>(null);
  const candlestickSeriesRef = useRef<ISeriesApi<'Candlestick'> | null>(null);
  const lineSeriesRef = useRef<ISeriesApi<'Line'> | null>(null);
  const priceLineRef = useRef<ReturnType<ISeriesApi<'Candlestick'>['createPriceLine']> | null>(null);
  const [bars, setBars] = useState<any[] | null>(null);
  const [prediction, setPrediction] = useState<any | null>(null);
  const [parameters, setParameters] = useState<Parameters>({
    symbol: searchParams.get('t') || 'AAPL',
    timeframe: searchParams.get('i') as Timeframe || '1Hour',
    patternLength: 15,
    resultLength: 20,
    accuracyPower: 2.2,
    volatilityAdjustment: 1.0,
  });
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [confidence, setConfidence] = useState<number | null>(null);
  const [status, setStatus] = useState<string>("");
  const navigate = useNavigate();

  useEffect(() => {
    setSearchParams({ t: parameters.symbol, i: parameters.timeframe });
  }, [parameters.symbol, parameters.timeframe, setSearchParams]);

  const fetchChartData = async () => {
    setError("");
    if (!parameters.symbol) return;

    try {
      const response = await axios.get(
        `${settings.api}/api/market-data/${encodeURIComponent(parameters.symbol)}?timeframe=${parameters.timeframe}`
      );

      const chartData = response.data.data;

      if (!chartData || chartData.length === 0) {
        setError('No data available for this symbol');
        return;
      }

      setBars(chartData);

      if (candlestickSeriesRef.current) {
        candlestickSeriesRef.current.setData(chartData);
      } else if (chartRef.current) {
        candlestickSeriesRef.current = chartRef.current.addCandlestickSeries({
          upColor: '#26a69a',
          downColor: '#ef5350',
          borderVisible: false,
          wickUpColor: '#26a69a',
          wickDownColor: '#ef5350',
        });
        candlestickSeriesRef.current.setData(chartData);
      }

    } catch (error) {
      console.error('Error fetching chart data:', error);
      setError('Failed to fetch data.');
    }
  };

  const drawPrediction = (pred?: any) => {
    if (!pred) pred = prediction;
    if (isLoading || !chartRef.current || !bars || !pred) {
      if (!bars) setError("Please fetch data before predicting.");
      return;
    }
    const lines = [];
    console.log(pred);

    const barInterval = multiplier_map[parameters.timeframe];
    let lastTime = bars[bars.length - 1].time;

    let lastPrice = bars[bars.length - 1].close;
    for (const cr of pred.result.candleRatios) {
      lastTime += barInterval;
      lines.push({ value: lastPrice, time: lastTime });
      lastPrice *= Math.pow(cr, parameters.volatilityAdjustment)
    }

    if (!lineSeriesRef.current) {
      lineSeriesRef.current = chartRef.current.addLineSeries({
        color: 'yellow',
        lineWidth: 2,
        priceLineVisible: false
      });
    }

    lineSeriesRef.current.setData(lines);
  }

  useEffect(() => { // get latest data
    if (!parameters.symbol || !parameters.timeframe) return;
  
    const intervalId = setInterval(async () => {
      try {
        const response = await axios.get(
          `${settings.api}/api/latest/${encodeURIComponent(parameters.symbol)}?timeframe=${parameters.timeframe}`
        );
        const open = response.data.open;

        // Update price line
        if (candlestickSeriesRef.current) {
          if (priceLineRef.current) {
            candlestickSeriesRef.current.removePriceLine(priceLineRef.current);
          }

          priceLineRef.current = candlestickSeriesRef.current.createPriceLine({
            price: open,
            color: '#f39c12',
            lineWidth: 2,
            lineStyle: 2,
            axisLabelVisible: true,
            title: 'B/A avg',
          });
        }

      } catch (error) {
        console.error('Live update failed:', error);
      }
    }, 10000);
  
    return () => {
      clearInterval(intervalId);
    };
  }, [parameters.symbol, parameters.timeframe, bars]);

  const fetchPrediction = async () => {
    setError("");
    if (isLoading || !chartRef.current || !bars) {
      if (!bars) setError("Please fetch data before predicting.");
      return;
    }

    setIsLoading(true);
    setPrediction(null); // Clear previous prediction
    setStatus("Starting...");

    try {
      const url = `${settings.api}/api/prediction-stream/${parameters.symbol}?timeframe=${parameters.timeframe}&patternLength=${parameters.patternLength}&resultLength=${parameters.resultLength}&accuracyPower=${parameters.accuracyPower}&volatilityAdjustment=${parameters.volatilityAdjustment}`;

      const evtSource = new EventSource(url);

      evtSource.onmessage = (event) => {
        let data = event.data;

        // If it's the final prediction (JSON string), parse and use it
        if (data.startsWith("{") && data.includes("result")) {
          try {
            const predictionObj = JSON.parse(data.replace(/'/g, '"'));
            setPrediction(predictionObj);
            drawPrediction(predictionObj);

            if (predictionObj.weight) {
              setConfidence(predictionObj.weight);
            }
            setStatus("Completed");
          } catch (err) {
            console.error("Failed to parse final prediction:", err);
          }
          evtSource.close();
          setIsLoading(false);
        } else if (data.startsWith("Progress:")) {
          // Extract percent
          const percentMatch = data.match(/Progress:\s*(\d+(\.\d+)?)/);
          if (percentMatch) {
            const percent = parseFloat(percentMatch[1]);
            setStatus(`${(percent).toFixed(2)}%`);
          }
        } else if (data.startsWith("Error:")) {
          setError(data);
          evtSource.close();
          setIsLoading(false);
        } else {
          console.log("SSE message:", data);
        }
      };

      evtSource.onerror = (err) => {
        console.error("SSE error:", err);
        setError("Connection error.");
        evtSource.close();
        setIsLoading(false);
      };
    } catch (err) {
      console.error("Fetch error:", err);
      setError("Failed to fetch prediction.");
      setIsLoading(false);
    }
  };

  useEffect(() => {
    if (!chartContainerRef.current) return;

    const chart = createChart(chartContainerRef.current, {
      layout: {
        background: { type: ColorType.Solid, color: '#121212' },
        textColor: '#d1d4dc',
      },
      grid: {
        vertLines: { color: '#2B2B43' },
        horzLines: { color: '#2B2B43' },
      },
      width: chartContainerRef.current.clientWidth,
      height: chartContainerRef.current.clientHeight,
      handleScale: {
        mouseWheel: true,
        pinch: true,
      },
      handleScroll: {
        mouseWheel: true,
        pressedMouseMove: true,
        horzTouchDrag: true,
        vertTouchDrag: true,
      },
    });

    const handleResize = () => {
      if (chartContainerRef.current) {
        const width = chartContainerRef.current.clientWidth;
        const height = chartContainerRef.current.clientHeight;
        
        // Ensure minimum dimensions
        const finalWidth = Math.max(width, 300);
        const finalHeight = Math.max(height, 300);
        
        chart.applyOptions({
          width: finalWidth,
          height: finalHeight,
        });
        
        // Force a resize event to ensure proper rendering
        chart.timeScale().fitContent();
      }
    };

    // Initial resize
    handleResize();

    // Add resize observer for more reliable resize detection
    const resizeObserver = new ResizeObserver(handleResize);
    if (chartContainerRef.current) {
      resizeObserver.observe(chartContainerRef.current);
    }

    window.addEventListener('resize', handleResize);
    chartRef.current = chart;

    const debounceTimeout = setTimeout(() => {
      fetchChartData();
    }, 1500);

    return () => {
      window.removeEventListener('resize', handleResize);
      if (chartContainerRef.current) {
        resizeObserver.unobserve(chartContainerRef.current);
      }
      resizeObserver.disconnect();
      clearTimeout(debounceTimeout);
      chart.remove();
      chartRef.current = null;
      candlestickSeriesRef.current = null;
      lineSeriesRef.current = null;
      priceLineRef.current = null;
    };
  }, [parameters.symbol, parameters.timeframe]);

  useEffect(drawPrediction, [parameters.volatilityAdjustment])

  const handleParameterChange = (field: keyof Parameters) => (
    event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement> | SelectChangeEvent
  ) => {
    const newValue = event.target.value;
    setParameters(prev => ({
      ...prev,
      [field]: newValue,
    }));
  };

  return (
    <TerminalContainer>
      <Box
        sx={{
          display: 'flex',
          flexDirection: { xs: 'column-reverse', md: 'row' },
          gap: 2,
          height: '100%',
        }}
      >
        <ParameterPanel elevation={3}>
          <Box onClick={() => navigate("/")}>
            <Logo>Tradeex Terminal</Logo>
          </Box>
          <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
            <TextField
              label="Symbol"
              value={parameters.symbol}
              onChange={handleParameterChange('symbol')}
              fullWidth
              placeholder="Enter ticker symbol (e.g., AAPL)"
              helperText="Stock ticker symbol, or a crypto symbol - prefixed with $"
            />
            <FormControl fullWidth>
              <InputLabel>Interval</InputLabel>
              <Select
                value={parameters.timeframe}
                label="Interval"
                onChange={handleParameterChange('timeframe')}
              >
                <MenuItem value="1Min">1 Minute</MenuItem>
                <MenuItem value="5Min">5 Minutes</MenuItem>
                <MenuItem value="15Min">15 Minutes</MenuItem>
                <MenuItem value="1Hour">1 Hour</MenuItem>
                <MenuItem value="4Hour">4 Hours</MenuItem>
                <MenuItem value="24Hour">1 Day</MenuItem>
              </Select>
            </FormControl>
            <Typography gutterBottom>
              Algorithm Settings
            </Typography>
            <TextField
              label="Pattern Length"
              value={parameters.patternLength}
              onChange={handleParameterChange('patternLength')}
              type="number"
              fullWidth
              inputProps={{ step: 1, min: 3, max: 100 }}
              helperText="Searching pattern length (bars)"
            />
            <TextField
              label="Result Length"
              value={parameters.resultLength}
              onChange={handleParameterChange('resultLength')}
              type="number"
              fullWidth
              inputProps={{ step: 1, min: 3, max: 100 }}
              helperText="Output length (bars)"
            />
            <TextField
              label="Accuracy Power"
              value={parameters.accuracyPower}
              onChange={handleParameterChange('accuracyPower')}
              type="number"
              fullWidth
              inputProps={{ step: 0.1, min: 1, max: 10 }}
              helperText="Sets model fit tightness"
            />
            <Accordion>
              <AccordionSummary
                expandIcon={<ExpandMoreIcon />}
                aria-controls="advanced-panel-content"
                id="advanced-panel-header"
              >
                <Typography>Advanced</Typography>
              </AccordionSummary>
              <AccordionDetails>
                <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
                  <Typography gutterBottom>
                    Volatility Adjustment: {parameters.volatilityAdjustment.toFixed(2)}
                  </Typography>
                  <Slider
                    value={parameters.volatilityAdjustment}
                    onChange={(_, value) => setParameters(prev => ({
                      ...prev,
                      volatilityAdjustment: value as number
                    }))}
                    min={0.1}
                    max={5}
                    step={0.01}
                    marks={[
                      { value: 0.1, label: '0.1' },
                      { value: 1, label: '1.0' },
                      { value: 5, label: '5' }
                    ]}
                  />
                  
                  {confidence !== null && (
                    <Box>
                      <Typography variant="subtitle2" gutterBottom>
                        Prediction Error
                      </Typography>
                      <Typography variant="h4" color="primary">
                        {(confidence * 100).toFixed(1)}PPE
                      </Typography>
                    </Box>
                  )}
                </Box>
              </AccordionDetails>
            </Accordion>
            <Button 
              variant="contained" 
              color="primary"
              onClick={fetchPrediction}
              disabled={isLoading}
            >
              {isLoading ? 'Predicting ' + status ? ` ${status}` : "..." : 'Predict'}
            </Button>
            <Typography>Chart Options</Typography>
            <Button
              variant="contained" 
              color="primary"
              onClick={async () => {await drawPrediction(); await fetchChartData()}}
              disabled={isLoading}
            >
              {isLoading ? 'Loading...' : 'Refresh'}
            </Button>
            {error && (
              <Typography color="error" variant="body2">
                {error}
              </Typography>
            )}
          </Box>
        </ParameterPanel>

        <ChartContainer elevation={3}>
          <Box sx={{ p: 2, borderBottom: 1, borderColor: 'divider', display: "flex", justifyContent: "space-between", alignItems: "center" }}>
            <Typography variant="h6">
              {parameters.symbol} - {parameters.timeframe} Chart
            </Typography>
            <Typography variant='body2'>Chart is not live</Typography>
          </Box>
          <ChartWrapper>
            <div 
              ref={chartContainerRef} 
              style={{ width: '100%', height: '100%' }}
            />
          </ChartWrapper>
        </ChartContainer>
      </Box>
    </TerminalContainer>
  );
};

export default TerminalPage;
